.preloader-wrapper {
  position: relative;
  width: 100px;
  height: 100px;
}

.preloader-border {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px rgba(33, 150, 243, 0.1);
}

.preloader-line-mask {
  position: absolute;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transform-origin: 50px 50px;
  mask-image: linear-gradient(to top, rgba(0, 0, 0, 0), black);
  animation: preloadSpinner 1.2s infinite linear;
}

.preloader-line {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px #2196f3;
}

@keyframes preloadSpinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.preloader-dark .preloader-border {
  box-shadow: inset 0 0 0 2px rgba(32, 32, 32, 0.1);
}

.preloader-dark .preloader-line {
  box-shadow: inset 0 0 0 2px rgba(32, 32, 32, 0.7);
}

.fancybox-container {
  z-index: 999999;
}

.page-header-block-height {
  /* stylelint-disable-next-line declaration-no-important */
  min-height: 360px !important;
}

.banner-slides-wrapper {
  width: 100%;
}

.banner-slides-container .owl-item:not(.active) .slide-animate {
  animation-name: none;
}

.banner-slides-container .owl-item img {
  width: auto;
}

.banner-slides-container .owl-dots {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: 0 0 25px;
}

.banner-slides-container .owl-nav {
  margin-top: 0;
}

.banner-slides-container .owl-nav .owl-prev,
.banner-slides-container .owl-nav .owl-next {
  display: none;
}

@media (min-width: 768px) {
  .page-header-block-height {
    /* stylelint-disable-next-line declaration-no-important */
    min-height: 600px !important;
  }
}

@media (min-width: 1280px) {
  .page-header-block-height {
    /* stylelint-disable-next-line declaration-no-important */
    min-height: 800px !important;
  }
}

.page-title {
  position: relative;
  min-height: 240px;
}

.page-title::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
}

.page-title.page-title-no-overlay::after {
  display: none;
}

.page-title-container {
  position: relative;
  z-index: 1;
}

@media (min-width: 768px) {
  .page-title {
    min-height: 360px;
  }
}

@media (min-width: 992px) {
  .page-title {
    min-height: 420px;
  }
}

.footer-bottom {
  position: relative;
}

.footer-bottom::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.3);
}

.footer-bottom.footer-bottom-no-overlay::after {
  display: none;
}

.footer-bottom-light::after {
  background-color: rgba(0, 0, 0, 0.03);
}

.footer-bottom-container {
  position: relative;
  z-index: 1;
}

